<template>
  <div>
    <Panel :panelData="panelData">
      <canvas id="province-trend" width="380" height="350"></canvas>
    </Panel>
  </div>
</template>

<script>
import Panel from "@/components/common/Panel";
import F2 from "@antv/f2/lib/index";
import { formatDate } from "@/assets/js/tools";

export default {
  name: "ProvinceTrend",
  components: {
    Panel
  },
  data() {
    return {
      panelData: {
        title: "",
        desc: "",
        status: ""
      },
      data: []
    };
  },
  mounted() {
    this.getAreaData();
    // this.createChart();
  },
  methods: {
    // 
    getAreaData() {
      const province = this.$api.charts.province;

      province().then(res => {
        const data = res.results;
        // console.log("", data);

        // 
        for (let i = 0; i < data.length; i++) {
          const date = formatDate(data[i].updateTime);
          data[i].updateTime = date;
        }

        //  
        const finalData = data.filter(item => {
          return item.country === "" && item.provinceShortName !== "";
        });

        this.data = finalData;
        this.createChart();
      });
    },

    // 
    createChart() {
      const data = this.data;
      data.forEach((obj, index) => {
        obj.index = index;
      });
      // console.log(data);

      const chart = new F2.Chart({
        id: "province-trend",
        pixelRatio: window.devicePixelRatio
      });
      chart.source(data, {
        index: {
          min: 0,
          max: 5
        }
      });
      chart.tooltip({
        onShow(ev) {
          const items = ev.items;
          const title = data[items[0].title].provinceShortName
          items[0].name = title;
          items[0].value = items[0].value + " ";
        }
      });
      chart
        .interval()
        .position("index*confirmedCount")
        .color("confirmedCount");

      // 
      //  index 
      // 
      chart.axis("index", {
        label(text) {
          return {
            fontSize: 14,
            text: data[text].provinceShortName
          };
        }
      });

      chart.interaction("pan");
      // 
      chart.scrollBar({
        mode: "x",
        xStyle: {
          offsetY: -5
        }
      });

      chart.render();

      // 
      // this.$antvTools.painColText(chart, data);
    }
  }
};
</script>


<style scoped>
.van-panel__content {
  padding: 0 !important;
}
</style>
