<template>
  <div>
    <van-panel :title="panelData.title" :desc="panelData.desc" :status="''+panelData.status">
      <div>
        <van-cell title=" " @click="areaToggle" />
        <canvas id="cure" width="380" height="350"></canvas>
      </div>
    </van-panel>

    <van-area
      v-show="showPicker"
      :area-list="areaList"
      @cancel="areaCancel"
      @confirm="areaConfirm"
      :columns-num="1"
      :visible-item-count="visibleItemCount"
    />
  </div>
</template>

<script>
import F2 from "@antv/f2";
import areaList from "@/assets/js/area";
import { Notify } from "vant";

export default {
  data() {
    return {
      visibleItemCount: 6,
      panelData: {
        title: "",
        desc: "",
        status: ""
      },
      data: [],
      areaList,
      province: "",
      showPicker: false
    };
  },

  mounted() {
    this.getAreaData();
  },

  methods: {
    areaToggle() {
      this.showPicker = !this.showPicker;
    },

    // 
    areaConfirm(v) {
      this.areaToggle();
      const province = v[0].name;
      this.province = province;
      this.getAreaData();
      this.panelData.status = province;
    },

    //
    areaCancel() {
      this.areaToggle();
    },

    // 
    getAreaData() {
      const area = this.$api.charts.area;

      area(this.province).then(res => {
        if (res.results[0] === undefined) {
          Notify("");
          return;
        }
        const data = res.results[0].cities;
        // console.log(res);

        this.data = data;
        this.createChart();
      });
    },

    // 
    createChart() {
      const data = this.data;

      const chart = new F2.Chart({
        id: "cure",
        padding: "auto",
        pixelRatio: window.devicePixelRatio // 
      });

      if (data.length > 7) {
        chart.axis("cityName", {
          label: {
            rotate: Math.PI / 3,
            textAlign: "start",
            textBaseline: "middle"
          }
        });
      }

      chart
        .source(data)
        .interval()
        .position("cityName*confirmedCount")
        .color("cityName");

      chart.tooltip({
        showItemMarker: true,
        background: {
          radius: 2,
          padding: [3, 5]
        },
        onShow(ev) {
          const items = ev.items;
          items[0].name = items[0].title;
          items[0].value = items[0].value + " ";
        }
      });

      chart.render();

      // 
      this.$antvTools.painColText(chart,data);
    }
  }
};
</script>

