<template>
  <div id="diagnose-wrap">
    <Panel :panelData="panelData">
      <canvas id="diagnose" width="380" height="350"></canvas>
    </Panel>
  </div>
</template>

<script>
import Panel from "@/components/common/Panel";
import F2 from "@antv/f2/lib/index";
import { formatDate } from "@/assets/js/tools";
import ScrollBar from "@antv/f2/lib/plugin/scroll-bar";
import pan from "@antv/f2/lib/interaction/pan";

export default {
  components: {
    Panel
  },
  data() {
    return {
      panelData: {
        title: "",
        desc: "24",
        status: ""
      },
      data: [
        // { genre: "122", sold: 571 },
        // { genre: "123", sold: 830 },
        { genre: "124", sold: 1287 },
        { genre: "125", sold: 1975 },
        { genre: "126", sold: 2744 },
        { genre: "127", sold: 4515 },
        { genre: "128", sold: 5974 },
        { genre: "129", sold: 7711 },
        { genre: "130", sold: 9097 }, // 01-31 757
        { genre: "131", sold: 9829 }, // 02-01 315
      ]
    };
  },
  mounted() {
    // this.getAreaData();
    this.createChart();
  },
  methods: {
    // 
    getAreaData() {
      const province = this.$api.charts.province;

      province("").then(res => {
        const data = res.results;
        console.log("", data);

        // 
        for (let i = 0; i < data.length; i++) {
          const date = formatDate(data[i].updateTime);
          data[i].updateTime = date;
        }
      });
    },

    // 
    createChart() {
      const data = this.data;

      const chart = new F2.Chart({
        id: "diagnose",
        plugins: [ScrollBar, pan],
        pixelRatio: window.devicePixelRatio
      });

      chart.source(data, {
        value: {
          tickCount: 5,
          min: 0
        },
        date: {
          type: "timeCat",
          range: [0, 1],
          tickCount: 3
        }
      });

      chart.tooltip({
        custom: true,
        showXTip: true,
        showYTip: true,
        snap: true,
        crosshairsType: "xy",
        crosshairsStyle: {
          lineDash: [2]
        }
      });

      chart.axis("date", {
        label: function label(text, index, total) {
          const textCfg = {};
          if (index === 0) {
            textCfg.textAlign = "left";
          } else if (index === total - 1) {
            textCfg.textAlign = "right";
          }
          return textCfg;
        }
      });

      chart.line().position("genre*sold");

      // 
      // chart
      //   .source(data)
      //   .interval()
      //   .position("genre*sold")
      //   .color("genre");

      chart.render();
    }
  }
};
</script>


<style scoped>
.van-panel__content {
  padding: 0 !important;
}
</style>
