import base from './base' // 
import { get } from '../https'

const charts = {
  // 20201243:40
  // 
  // ///
  // latest	1 0
  // province	
  area(province) {
    return get(
      `${base.sq}/area`,
      { latest: 1, province }
    );
  },

  // 
  province() {
    return get(
      `${base.sq}/area`,
    );
  }
}

export default charts
