<template>
  <div>
    <van-nav-bar title="" fixed />
    <Panel :panelData="panelData">
      <Skeleton :loading="loading">
        <van-cell-group>
          <!-- <van-cell title="" :value="message.generalRemark">
            <van-icon slot="icon" name="volume-o" size="1.5em" style="line-height: 1.2em;" />
          </van-cell> -->

          <van-collapse accordion v-model="activeNames">
            <van-collapse-item icon='volume-o' title="" name="generalRemark">{{message.generalRemark}}</van-collapse-item>
            <van-collapse-item icon='warn-o' title="" name="virus">{{message.virus}}</van-collapse-item>
            <van-collapse-item icon='warn-o' title="" name="infectSource">{{message.infectSource}}</van-collapse-item>
            <van-collapse-item icon='warn-o' title="" name="passWay">{{message.passWay}}</van-collapse-item>
            <van-collapse-item icon='friends-o' title="" name="remark1">{{message.remark1}}</van-collapse-item>
            <van-collapse-item icon='todo-list-o' title="" name="remark2">{{message.remark2}}</van-collapse-item>
          </van-collapse>

          <van-cell title="" icon="warning-o" :value="message.confirmedCount" />
          <van-cell title="" icon="question-o" :value="message.suspectedCount" />
          <van-cell title="" icon="smile-o" :value="message.curedCount" />
          <van-cell title="" icon="close" :value="message.deadCount" />
          <van-cell title="" icon="clock-o" :value="message.updateTime" />
        </van-cell-group>
      </Skeleton>
    </Panel>
  </div>
</template>

<script>
import Panel from "@/components/common/Panel";
import Skeleton from "@/components/common/Skeleton";
import { formatDate } from "@/assets/js/tools";

export default {
  components: {
    Panel,
    Skeleton
  },

  data() {
    return {
      activeNames: 'generalRemark',
      panelData: {
        title: "",
        desc: "",
        status: ""
      },
      message: {
        infectSource: "",
        passWay: "",
        confirmedCount: "",
        suspectedCount: "",
        curedCount: "",
        deadCount: "",
        virus: "",
        remark1: "",
        remark2: "",
        generalRemark: "",
        updateTime: ""
      },
      loading: true
    };
  },

  mounted() {
    this.getMessage();
  },

  methods: {
    // 
    getMessage() {
      const overall = this.$api.message.overall;

      overall().then(res => {
        ({
          infectSource: this.message.infectSource,
          passWay: this.message.passWay,
          confirmedCount: this.message.confirmedCount,
          suspectedCount: this.message.suspectedCount,
          curedCount: this.message.curedCount,
          deadCount: this.message.deadCount,
          virus: this.message.virus,
          remark1: this.message.remark1,
          remark2: this.message.remark2,
          generalRemark: this.message.generalRemark,
          updateTime: this.message.updateTime
        } = res.results[0]);

        const updateTime = this.message.updateTime;
        this.message.updateTime = formatDate(updateTime, "Ymd");

        this.loading = false;
      });
    }
  }
};
</script>

<style scoped>
</style>
